.App {
  text-align: center;
}
.background {
  min-height: 100%;
  background-color : #d5bed5;
  background-size: cover;
  background-repeat: no-repeat;

  width: 100vw;
  height: 100vh;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.text{
    min-height: 90vh;
    display: flex;
    align-items: center;
    justify-content: center;
    text-justify: center;

}
